import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Modal, Input } from 'antd';
import HotKeysModal from "Components/HotKeysModal";
import { DeferFn, langCheck } from 'Pub/js/utils';
import Ajax from 'Pub/js/ajax.js';
import Notice from 'Components/Notice';

class ReNameTemplet extends Component {
    constructor(props) {
        super(props);
        this.state = {
            templateName: this.props.templateNameVal,
            nameErrorMsg: '',
        };
    }
    componentDidMount() {}


    trim = (str) => {
        if (str) {
            return str.replace(/^\s+|\s+$/g, '');
        }
    };
    
    checkName = () => {
        let { json, templateNameVal } = this.props;
        let { templateName } = this.state;
        // setCopyTemplateNameVal(templateName);
        if (!this.trim(templateName)) {
            document.getElementById('templateName').classList.add('has-error');
            this.setState({
                nameErrorMsg: langCheck('10180TM-000023', "pages", json) //名称不能为空
            });
            return;
        }
        this.setState({
            nameErrorMsg: ''
        });
        document.getElementById('templateName').classList.remove('has-error');
    };

    handleOk = () => {
        let {
            templatePk,
            templateNameVal,
            json,
            saveLeftArrTS
        } = this.props;
        let { templateName } = this.state;
        if (!this.trim(templateNameVal)) {
            return;
        }
        if (templateName == '') {
            return;
        };
        let ts = '';
        //不同情况的TS值保存
        if (saveLeftArrTS.length == 1) {
            saveLeftArrTS.map((item) => {
                ts = item.ts;
            });
        } else if (saveLeftArrTS.length > 1) {
            saveLeftArrTS.map((itemPk) => {
                if (templatePk == itemPk.templateId) {
                    ts = itemPk.ts;
                }
            });
        }
        document.getElementById('templateName').classList.remove('has-error');
        let infoData = {
            templateId: templatePk,
            templateName: templateName,
            ts,
        };
        let url = `/nccloud/platform/template/resetTemplateNameAction.do`;
        var _this = this;
        DeferFn(() => {
            Ajax({
                url: url,
                data: infoData,
                loading: true,
                info: {
                    name: langCheck('10180TM-000012', "pages", json) /* 国际化处理： 模板设置*/,
                    action: langCheck('10180TM-000025', "pages", json) /* 国际化处理： 模板复制*/
                },
                success: ({ data }) => {
                    if (data.success) {
                        Notice({ status: 'success', msg: data.msg });
                        _this.props.reqTreeTemData('rename');
                        _this.handleCancel();
                    }
                }
            });
        });
    }


    handleCancel = () => {
        this.props.setReNameModal(false);
    };

    onChangeTemplateName = (e) => {
        this.setState({
            templateName: e.target.value
        });
        // this.props.setCopyTemplateNameVal(templateName);
    }

    render() {
        let { json, reNameModalVisible, templateNameVal } = this.props;
        let { templateName, nameErrorMsg } = this.state;
        console.log('templateNameVal', templateNameVal);
        return (
            <HotKeysModal
                mask={true}
                // closable={false}
                title={langCheck('10180TM-000056', "pages", json)} /* 国际化处理： 重命名*/
                visible={reNameModalVisible}
                height={260}
                onOk={this.handleOk}
                okText={langCheck('10180TM-000015', "pages", json)} /* 国际化处理： 确定*/
                cancelText={langCheck('10180TM-000016', "pages", json)} /* 国际化处理： 取消*/
                centered={true}
                onCancel={this.handleCancel}
                maskClosable={false}
                fieldid="rename"
            >
                <div className='copyTemplate' style={{padding: '60px 20px'}}>
                    <div className='templateName'>
                        <label htmlFor=''>{langCheck('10180TM-000029', "pages", json)}</label>
                        {/* 国际化处理： 模板名称：*/}
                        <Input
                            value={templateName}
                            defaultValue={templateNameVal}
                            id='templateName'
                            style={{ width: '80%' }}
                            onBlur={this.checkName}
                            placeholder={langCheck('10180TM-000030', "pages", json)} /* 国际化处理： 模板名称*/
                            fieldid="templateName" //自动化测试标记
                            onChange={this.onChangeTemplateName}
                        />
                        <p className='areaName-errorMsg'>{nameErrorMsg}</p>
                    </div>
                </div>
            </HotKeysModal>
        );
    }
}


ReNameTemplet.propTypes = {
    templatePk: PropTypes.string.isRequired,
    pageCode: PropTypes.string.isRequired,
    appCode: PropTypes.string.isRequired,
    def1: PropTypes.string.isRequired,
    templateTitleVal: PropTypes.string.isRequired,
    templateNameVal: PropTypes.string.isRequired,
    json: PropTypes.object.isRequired
};
export default connect(
    (state) => ({
        templatePk: state.TemplateSettingData.templatePk,
        pageCode: state.TemplateSettingData.pageCode,
        appCode: state.TemplateSettingData.appCode,
        def1: state.TemplateSettingData.def1,
        templateTitleVal: state.TemplateSettingData.templateTitleVal,
        templateNameVal: state.TemplateSettingData.templateNameVal,
        treeTemPrintData: state.TemplateSettingData.treeTemPrintData,
        json: state.TemplateSettingData.json
    }),
    {
        // setCopyTemplateNameVal
    }
)(ReNameTemplet);
